body {
  margin: 0;
  font-family: sans-serif;
  overflow-y: scroll;
}

h1 {
  margin: 0;
  font-size: 2.25em;
}

p {
  margin: 1em 0 1.25em;
  font-size: 1.25em;
}

a {
  -webkit-text-decoration: underline dotted gray;
  text-decoration: underline dotted gray;
}

a:hover, a:focus {
  opacity: .75;
}

sub {
  font-size: 60%;
