#container {
  max-width: 1200px;
  margin: 24px auto;
}

#calculator {
  width: 100%;
  height: 600px;
}

#count {
  color: red;
  text-align: center;
  margin: 6px 0 3px;
  font-size: 3em;
}

#tree {
  color: #0b6ba1;
  text-align: center;
  border: 2px solid #ccc;
  font-size: 2em;
  overflow-x: scroll;
}

#tree:empty {
  display: none;
}

#tree ul {
  padding: 0;
  display: flex;
}

#tree > ul {
  display: inline-flex;
}

#tree li {
  display: block;
  position: relative;
}

#tree li:before {
  content: "";
  height: 2px;
  background-color: #54bbf7;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

#tree li:first-child:before {
  left: 50%;
}

#tree li:last-child:before {
  right: 50%;
}

#tree div {
  width: 100px;
  height: 100px;
  background-color: #caf0ff;
  border: 1px solid #54bbf7;
  border-radius: 50px;
  margin: 20px;
  line-height: 100px;
  display: inline-block;
  position: relative;
}

#tree div:before {
  content: "";
  z-index: -1;
  background-color: #54bbf7;
  display: block;
  position: absolute;
  top: -21px;
  bottom: -21px;
  left: 49px;
  right: 49px;
}

#tree > ul > li > div:before {
  top: 0;
}

#tree div:only-child:before {
  bottom: 0;
}

#tree li > span {
  justify-content: center;
  display: flex;
  position: relative;
}

#tree li > span:before {
  content: "";
  width: 2px;
  background-color: #54bbf7;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: calc(50% - 1px);
}

#tree > ul > li > span:before {
  top: 50%;
}

#tree li > span > div:before {
  content: "";
  height: 2px;
  background-color: #54bbf7;
  display: block;
  position: absolute;
  top: 49px;
  left: -21px;
  right: -21px;
}

#tree li > span > div:first-child:before {
  left: 50%;
}

#tree li > span > div:last-child:before {
  right: 50%;
}

.fraction {
  vertical-align: middle;
  flex-direction: column;
  line-height: 40px;
  display: inline-flex;
}

.fraction span:first-child {
  border-bottom: 2px solid #0b6ba1;
}

